                >
                  <span class="ic-close h6"></span>
                </button>
              </div>
              <ul class="navbar-nav">
                <li
                  v-for="data in menuList"
                  :key="data.index"
                  :class="{ dropdown: data.isDropdown }"
                >
                  <template v-if="data.isDropdown">
                    <a
                      class="dropdown-toggle"
                      type="button"
                      id="navbarDropdown"
                      role="button"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      {{ data.title }}
                    </a>
                    <ul
                      class="dropdown-menu dropdown-menu-right animate slideIn"
                      aria-labelledby="navbarDropdown"
                    >
                      <li
                        v-for="item in data.innerMenu"
                        :key="item.index"
                        :class="{ inner: item.hasSecondLevel }"
                      >
                        <template v-if="item.hasSecondLevel">
                          <a class="dropdown-item">
                            {{ item.innerTitle }}
                          </a>
                          <div
                            class="dropdown-menu-inner animate slideIn"
                            aria-labelledby="navbarinnerDropdown"
                          >
                            <router-link
                              v-for="innerItem in item.secondLevelMenu"
                              :key="innerItem.index"
                              :to="innerItem.link"
                              class="dropdown-item"
                              >{{ innerItem.secondLeveltitle }}</router-link
                            >
                          </div>
                        </template>
                        <template v-else>
                          <router-link class="dropdown-item" :to="item.link">
                            {{ item.innerTitle }}
                          </router-link>
                        </template>
                      </li>
                    </ul>
                  </template>
                  <template v-else>
                    <router-link
                      :to="data.link"
                      :class="{ active: data.active }"
                    >
                      {{ data.title }}
                    </router-link>
                  </template>
