<script>
  import $ from "jquery";
  import NewsSticker from "./NewsSticker";
  export default {
    name: "Header",
    components: { NewsSticker },
    created() {
      window.addEventListener("scroll", this.scrollFunction);
    },
    destroyed() {
      window.removeEventListener("scroll", this.scrollFunction);
    },
    methods: {
      navbarToggle() {
        let target = document.querySelector(".navbar-collapse");
        let body = document.querySelector("body");
        target.classList.toggle("show");
        body.classList.toggle("overflowhide");
      },
      displaySearchBar() {
        let searchBar = document.querySelector(".search-card");
        let searchBar2 = document.querySelector(".search-card-sm");
        let searchInput = document.querySelector(".search-card input");
        let searchInput2 = document.querySelector(".search-card-sm input");
        let close = document.querySelector(".close");
        searchBar.classList.toggle("toggle");
        searchInput.focus();
        searchBar2.classList.toggle("toggle");
        searchInput2.focus();
        close.classList.toggle("toggle");
      },
      scrollFunction() {
        if (
          document.body.scrollTop > 20 ||
          document.documentElement.scrollTop > 20
        ) {
          this.scrollIndicator = false;
        } else {
          this.scrollIndicator = true;
        }
      },
    },
    mounted() {
      if (screen.width > 1008) {
        $(".dropdown").hover(
          function() {
            $(this).addClass("show");
            $(this)
              .find(".dropdown-menu")
              .addClass("show");
          },
          function() {
            $(this).removeClass("show");
            $(this)
              .find(".dropdown-menu")
              .removeClass("show");
          }
        );
      }
    },
    data() {
      return {
        scrollIndicator: true,
        menuList: [
