<template>
  <main class="pbottom-72 bg-gray-96 ">
    <InnerHero
      :img="bannerImg"
      title="SIP Calculator"
      des="An online financial tool that can help to calculate the returns you would earn on your SIP investments."
      :nav="navs"
    />
    <section class="section-sip">
      <div class="container">
        <div class="row">
          <div class="col-lg-8">
            <div class="card p-4 p-md-5">
              <div class="form mb-3">
                <h6 class="mb-3 ">Investment Period</h6>
                <div class="form-group">
                  <div class="custom-radio">
                    <input
                      label="Annually"
                      type="radio"
                      id="Annually"
                      name="period"
                      value="Annually"
                      checked
                    />
                    <input
                      label="Semi-Annually"
                      type="radio"
                      id="Semi-Annually"
                      name="period"
                      value="Semi-Annually"
                    />
                    <input
                      label="Quarterly"
                      type="radio"
                      id="Quarterly"
                      name="period"
                      value="Quarterly"
                    />
                    <input
                      label="Monthly"
                      type="radio"
                      id="Monthly"
                      name="period"
                      value="Monthly"
                    />
                  </div>
                </div>
                <div class="form-group">
                  <div class="flex-center-between mb-3">
                    <h6>Annual Investment (In NPR)</h6>
                    <input
                      type="text"
                      class="form-control form-control-sm "
                      :value="value"
                    />
                  </div>
                  <input
                    type="range"
                    min="1"
                    max="100000"
                    step="1"
                    v-model="value"
                    @input="updateSlider"
                    :style="{ backgroundSize: backgroundSize }"
                  />
                </div>
                <div class="form-group">
                  <div class="flex-center-between mb-3">
                    <h6>Expected Annual Returns (%)</h6>

                    <input
                      type="text"
                      class="form-control form-control-sm "
                      :value="value2"
                    />
                  </div>
                  <input
                    type="range"
                    min="1"
                    max="8"
                    step="1"
                    v-model="value2"
                    @input="updateSlider2"
                    :style="{ backgroundSize: backgroundSize2 }"
                  />
                </div>
                <div class="form-group">
                  <div class="flex-center-between mb-3">
                    <h6>Years</h6>
                    <input
                      type="text"
                      class="form-control form-control-sm "
                      :value="value3"
                    />
                  </div>
                  <input
                    type="range"
                    min="1"
                    max="20"
                    step="1"
                    v-model="value3"
                    @input="updateSlider3"
                    :style="{ backgroundSize: backgroundSize3 }"
                  />
                </div>
              </div>

              <h6 class="text-secondary">Result</h6>
              <ul class="list">
                <li class="flex-center-between mt-3">
                  <p>Amt Invested</p>
                  <p class="font-bold">Rs. 120000.00</p>
                </li>
                <li class="flex-center-between mt-3">
                  <p>Expected Amt</p>
                  <p class="font-bold">Rs. 12130797.76</p>
                </li>
                <li class="flex-center-between mt-3">
                  <p>Wealth Gain</p>
                  <p class="font-bold">Rs. 130797.76</p>
                </li>
              </ul>
            </div>
          </div>
          <div class="col-lg-4">
            <div class="card align-center text-center mt-4 mt-lg-0 p-4 p-md-5">
              <p class="font-bold">Annually SIP Amount</p>
              <h3 class="text-secondary my-3">Rs. 130797.76</h3>
              <p>
                Start your SIP Investment now. Open your gateway for passive
                earning.
              </p>
              <button class="btn btn-block btn-primary mt-4">Invest Now</button>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
  // import $ from "jquery";
  import InnerHero from "../../components/InnerHero";
  require("../../../node_modules/ion-rangeslider/js/ion.rangeSlider.min.js");
  // require("../../../node_modules/ion-rangeslider/css/");
  import bannerImg from "../../assets/img/products/banner.jpg";
  export default {
    name: "SipCalculator",
    components: { InnerHero },
    data() {
      return {
        bannerImg,
        value: 50000,
        backgroundSize: "50% 100%",
        value2: 2,
        backgroundSize2: "15% 100%",
        value3: 10,
        backgroundSize3: "47% 100%",
        navs: [{ menu: "SIP Calculator" }],
      };
    },
    methods: {
      updateSlider(e) {
        let clickedElement = e.target,
          min = clickedElement.min,
          max = clickedElement.max,
          val = clickedElement.value;

        this.backgroundSize = ((val - min) * 100) / (max - min) + "% 100%";
      },
      updateSlider2(e) {
        let clickedElement = e.target,
          min = clickedElement.min,
          max = clickedElement.max,
          val = clickedElement.value;

        this.backgroundSize2 = ((val - min) * 100) / (max - min) + "% 100%";
      },
      updateSlider3(e) {
        let clickedElement = e.target,
          min = clickedElement.min,
          max = clickedElement.max,
          val = clickedElement.value;

        this.backgroundSize3 = ((val - min) * 100) / (max - min) + "% 100%";
      },
    },
  };
</script>

