<template>
  <div id="fullpage">
    <SidebarIcons />
    <HeroSection />
    <Service />
    <Product />
    <About />
    <Mutual />
    <Subsidiary />
    <News />
  </div>
</template>

<script>
  import HeroSection from "./HeroSection";
  import Service from "./Service";
  import Product from "./Product";
  import About from "./About";
  import Mutual from "./Mutual";
  import Subsidiary from "./Subsidiary";
  import News from "./News";
  import SidebarIcons from "./SidebarIcons";
  import anime from "animejs/lib/anime.es.js";

  export default {
    name: "Home",
    components: {
      SidebarIcons,
      HeroSection,
      Service,
      Product,
      About,
      Mutual,
      News,
      Subsidiary,
    },
    mounted() {
      const heroSection = document.querySelector("#animate-hero");
      if (heroSection) {
        const heroObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                // easing: "cubicBezier(0.83, 0, 0.17, 1)",
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-hero",
                opacity: [0, 1],
                delay: "1000",
              })
              .add(
                {
                  targets: ".hero-textbox",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 50 * i,
                },
                "-=1000"
              )
              .add(
                {
                  targets: ".hero-news",
                  duration: 700,
                  translateY: [200, 0],
                  opacity: [0, 1],
                },
                "-=1400"
              );
            heroObserver.unobserve(entries[0].target);
          }
        });
        heroObserver.observe(heroSection);
      }

      const serviceSection = document.querySelector("#animate-service");
      if (serviceSection) {
        let options = {
          rootMargin: "-100px",
        };
        const serviceObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-service",
                opacity: [0, 1],
              })
              .add(
                {
                  targets: ".textbox-service",
                  opacity: [0, 1],
                  translateY: [100, 0],
                },
                "-=500"
              )
              .add(
                {
                  targets: ".media-service",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 100 * i,
                },
                "-=500"
              );
            serviceObserver.unobserve(entries[0].target);
          }
        }, options);
        serviceObserver.observe(serviceSection);
      }

      const product01Section = document.querySelector("#animate-product-01");
      if (product01Section) {
        let options = { rootMargin: "-100px" };
        const product01Observer = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-product-01",
                opacity: [0, 1],
              })
              .add(
                {
                  targets: ".textbox-product-01",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 150 * i,
                },
                "-=500"
              );

            product01Observer.unobserve(entries[0].target);
          }
        }, options);
        product01Observer.observe(product01Section);
      }

      const product02Section = document.querySelector("#animate-product-02");
      if (product02Section) {
        let options = { rootMargin: "-100px" };
        const product02Observer = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-product-02",
                opacity: [0, 1],
              })
              .add(
                {
                  targets: ".media-news",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 150 * i,
                },
                "-=500"
              );
            product02Observer.unobserve(entries[0].target);
          }
        }, options);
        product02Observer.observe(product02Section);
      }

      const aboutSection = document.querySelector("#animate-about");
      if (aboutSection) {
        const aboutObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-about",
                opacity: [0, 1],
              })
              .add(
                {
                  targets: ".media-about",
                  duration: 1200,
                  opacity: [0, 1],
                  translateY: [200, 0],
                },
                "-=500"
              )
              .add(
                {
                  targets: ".textbox-about",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 65 * i,
                },
                "-=1200"
              );

            aboutObserver.unobserve(entries[0].target);
          }
        });
        aboutObserver.observe(aboutSection);
      }

      const mutualSection = document.querySelector("#animate-mutual");
      if (mutualSection) {
        const mutualObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-mutual",
                opacity: [0, 1],
              })

              .add(
                {
                  targets: ".textbox-mutual",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 65 * i,
                },
                "-=500"
              )
              .add(
                {
                  easing: "cubicBezier(0.83, 0, 0.17, 1)",
                  targets: ".media-mutual",
                  opacity: [0, 1],
                  translateY: [200, 0],
                },
                "-=1600"
              );

            mutualObserver.unobserve(entries[0].target);
          }
        });
        mutualObserver.observe(mutualSection);
      }

      const subsidiarySection = document.querySelector("#animate-subsidiary");
      if (subsidiarySection) {
        const subsidiaryObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-subsidiary",
                opacity: [0, 1],
              })
              .add(
                {
                  easing: "cubicBezier(0.83, 0, 0.17, 1)",
                  targets: ".media-subsidiary",
                  translateY: [200, 0],
                  opacity: [0, 1],
                },
                "-=500"
              )
              .add(
                {
                  targets: ".textbox-subsidiary",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 65 * i,
                },
                "-=500"
              );

            subsidiaryObserver.unobserve(entries[0].target);
          }
        });
        subsidiaryObserver.observe(subsidiarySection);
      }

      const newsSection = document.querySelector("#animate-news");
      if (newsSection) {
        const newsObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-news",
                opacity: [0, 1],
              })
              .add(
                {
                  targets: ".textbox-news",
                  opacity: [0, 1],
                  translateY: [100, 0],
                },
                "-=500"
              )
              .add(
                {
                  targets: ".media-news",
                  opacity: [0, 1],
                  translateY: [100, 0],
                  delay: (el, i) => 100 * i,
                },
                "-=800"
              );
            newsObserver.unobserve(entries[0].target);
          }
        });
        newsObserver.observe(newsSection);
      }

      const noticeSection = document.querySelector("#animate-notice");
      if (noticeSection) {
        const noticeObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-notice",
                opacity: [0, 1],
              })
              .add(
                {
                  targets: ".textbox-notice",
                  opacity: [0, 1],
                  translateY: [100, 0],
                },
                "-=1000"
              )
              .add(
                {
                  targets: ".media-notice",
                  translateY: [200, 0],
                  opacity: [0, 1],
                  delay: (el, i) => 100 * i,
                },
                "-=1000"
              );
            noticeObserver.unobserve(entries[0].target);
          }
        });
        noticeObserver.observe(noticeSection);
      }
    },
  };
</script>
