<template>
  <div id="app">
    <Notice />
    <Header class="interstitial-section" />
    <router-view></router-view>
    <Footer />
    <ScrollToTop />

    <!-- <Contact :class="pathName === '/' ? 'scroll-section' : 'd-none'" /> -->
  </div>
</template>

<script>
  import Footer from "./components/Footer";
  import Header from "./components/Header";
  import Notice from "./components/Notice";
  import anime from "animejs/lib/anime.es.js";
  import ScrollToTop from "./components/ScrollToTop";

  // import Contact from "./views/Home/Contact";
  import "bootstrap/dist/js/bootstrap";

  export default {
    //https://bootstrap-vue.org/docs
    name: "App",
    components: { Footer, Header, Notice, ScrollToTop },
    data() {
      return {
        pathName: "",
      };
    },
    mounted() {
      this.pathName = this.$route.path;
      const footerSection = document.querySelector("#animate-footer");
      if (footerSection) {
        const footerObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-footer",
                opacity: [0, 1],
              })

              .add({
                targets: ".textbox-footer",
                opacity: [0, 1],
                translateY: [100, 0],
                delay: (el, i) => 65 * i,
              });

            footerObserver.unobserve(entries[0].target);
          }
        });
        footerObserver.observe(footerSection);
      }
    },
  };
</script>

<style lang="scss">
  @import "./assets/scss/main";
</style>
