<template>
  <main class="page-product pbottom-48">
    <InnerHero
      :img="bannerImg"
      title="Portfolio Management Service"
      des="Portfolio management refers to managing the asset of an investor under the expert guidance of portfolio managers."
      :nav="navs"
    />
    <div class="container">
      <div class="row flex-center">
        <div class="col-md-10">
          <div class="flex-center-between mb-3">
            <h4 class="text-primary">Discretionary PMS Schemes</h4>
            <router-link to="/pms" class="align-center text-primary"
              ><i class="ic-chevron-left mr-2"></i>Back</router-link
            >
          </div>
          <div class="textbox">
            <p>
              Portfolio management refers to managing the asset of an investor
              under the expert guidance of portfolio managers. At Muktinath, we
              select the right investment policy for our clients to maximize
              return while minimizing ensuing risks
            </p>
          </div>

          <div id="accordion" class="accordion-01 mt-4">
            <div
              v-for="data in faqList"
              :key="data.index"
              class="accordion-item"
            >
              <h5
                data-toggle="collapse"
                :data-target="'#collapse' + data.id"
                :aria-expanded="data.id == 1 ? 'true' : 'false'"
                :aria-controls="'collapse' + data.id"
                class="h6 text-dark"
              >
                {{ data.question }}
              </h5>

              <div
                :id="'collapse' + data.id"
                class="collapse text-box"
                :class="data.id == 1 ? 'show' : ''"
                data-parent="#accordion"
              >
                <p class="mt-4">
                  {{ data.answer }}
                </p>
              </div>
            </div>
          </div>
          <h3 class="text-primary mtop-32">
            Other Services
          </h3>
          <div class="row">
            <div
              v-for="data in productList"
              :key="data.index"
              class=" col-sm-6 mt-4"
            >
              <a href="#">
                <div class="img-landscape">
                  <img :src="data.img" alt="" />
                </div>
                <div class="p-4 bg-gray-96">
                  <p class="h6 font-medium m-0">{{ data.title }}</p>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
  import InnerHero from "../../components/InnerHero";
  import bannerImg from "../../assets/img/products/banner.jpg";
  import securityUnderwritingBanner from "../../assets/img/img4.jpg";
  import rtsBanner from "../../assets/img/img6.jpg";
  export default {
    name: "PMSScheme",
    components: { InnerHero },
    data() {
      return {
        bannerImg,
        faqList: [
          {
            id: "1",
            question: "Muktinath Performance Based Portfolio",
            answer: "Fee is charged purely on Portfolio Manager's performance.",
          },
          {
            id: "2",
            question: "Muktinath Hurdle Based Portfolio",
            answer: "Fee is charged only above hurdle rate.",
          },
          {
            id: "3",
            question: "Muktinath Guaranteed Scheme",
            answer:
              "Guaranteed income as per the prevailing market rate is assured",
          },
          {
            id: "4",
            question: "Muktinath Fixed Fee Portfolio",
            answer:
              "Client is charged only fixed fee. No performance fee is charged.",
          },
          {
            id: "5",
            question: "Muktinath Customized Scheme",
            answer:
              "Client is charged only fixed fee. No performance fee is charged.",
          },
        ],
        navs: [
          { menu: "Products & Services" },
          { menu: "Portfolio Management Service" },
          { menu: "Muktinath Composite Scheme" },
        ],
        productList: [
          {
            img: securityUnderwritingBanner,
            title: "Venture Promotion",
          },
          {
            img: rtsBanner,
            title: "Advisory Service",
          },
        ],
      };
    },
  };
</script>
