<template>
  <main class="page-product pbottom-48">
    <InnerHero
      :img="bannerImg"
      title="Depository Participant"
      des="We are a licensed Depository Participant (DP) from CDS and Clearing Limited.As DP, we act as intermediaries between "
      :nav="navs"
    />
    <div class="container">
      <div class="row flex-center">
        <div class="col-md-10">
          <div class="textbox">
            <p>
              We are a licensed Depository Participant (DP) from CDS and
              Clearing Limited. As DP, we act as intermediaries between CDS and
              the investors who hold Demat account with us. Demat account lets
              investors convert their physical shares into electronic copy that
              is centrally registerd with CDS and Clearing Limited. This
              eliminates the risk associated with holding a paper based shares.
            </p>
            <h6>We provide the following services:</h6>
            <ul>
              <li>
                Dematerialization (known as demat) or converting of physical
                certificates of securities to electronic form
              </li>
              <li>
                Rematerialization (know as remat) is reverse of demat, or
                getting physical certificates from the electronic securities.
              </li>
              <li>Fast and electronic transfer of securities</li>
              <li>
                Change of beneficial ownership (family transfer, death transfer)
              </li>
            </ul>
          </div>
          <h3 class="text-primary mtop-32">
            Other Services
          </h3>
          <div class="row">
            <div
              v-for="data in productList"
              :key="data.index"
              class=" col-sm-6 mt-4"
            >
              <a href="#">
                <div class="img-landscape">
                  <img :src="data.img" alt="" />
                </div>
                <div class="p-4 bg-gray-96">
                  <p class="h6 font-medium m-0">{{ data.title }}</p>
                </div>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
  import InnerHero from "../../components/InnerHero";
  import bannerImg from "../../assets/img/products/banner.jpg";
  import pmsBanner from "../../assets/img/img5.jpg";
  import advisoryBanner from "../../assets/img/img3.jpg";
  export default {
    name: "Depository",
    components: { InnerHero },
    data() {
      return {
        bannerImg,
        navs: [
          { menu: "Products & Services" },
          { menu: "Depository Participant" },
        ],
        productList: [
          {
            img: pmsBanner,
            title: "Portfolio Management Services",
          },
          {
            img: advisoryBanner,
            title: "Advisory Service",
          },
        ],
      };
    },
  };
</script>
