import Vue from "vue";
import Router from "vue-router";
import Home from "../views/Home/Home";
import WhoWeAre from "../views/About/WhoWeAre/WhoWeAre";
import BOD from "../views/About/BOD/BOD";
import Team from "../views/About/Team/Team";
import IssueManagement from "../views/Products/IssueManagement";
import Depository from "../views/Products/Depository";
import PMS from "../views/Products/PMS";
import PMSScheme from "../views/Products/PMSScheme";
import Article from "../views/Information/Article";
import News from "../views/Information/News";
import Notice from "../views/Information/Notice";
import Gallery from "../views/Information/Gallery";
import Contact from "../views/Contact/Contact";
import Allotment from "../views/Allotment/Allotment";
import FAQ from "../views/FAQ/Faq";
import Download from "../views/Downloads/Download";
import Details from "../views/Career/Details";
import Listing from "../views/Career/Listing";
import SipCalculator from "../views/SIP/SipCalculator";
import Search from "../views/Search/Search";
import Splashscreen from "../views/Splashscreen/Splashscreen";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
    },
    {
      path: "/who",
      name: "WhoWeAre",
      component: WhoWeAre,
    },
    {
      path: "/bod",
      name: "BOD",
      component: BOD,
    },
    {
      path: "/team",
      name: "Team",
      component: Team,
    },
    {
      path: "/issue",
      name: "IssueManagement",
      component: IssueManagement,
    },
    {
      path: "/dp",
      name: "Depository",
      component: Depository,
    },
    {
      path: "/pms",
      name: "PMS",
      component: PMS,
    },
    {
      path: "/pms-scheme",
      name: "PMSScheme",
      component: PMSScheme,
    },
    {
      path: "/gallery",
      name: "Gallery",
      component: Gallery,
    },
    {
      path: "/article",
      name: "Article",
      component: Article,
    },
    {
      path: "/news",
      name: "News",
      component: News,
    },
    {
      path: "/notice",
      name: "Notice",
      component: Notice,
    },
    {
      path: "/contact",
      name: "Contact",
      component: Contact,
    },
    {
      path: "/allotment",
      name: "Allotment",
      component: Allotment,
    },
    {
      path: "/faq",
      name: "FAQ",
      component: FAQ,
    },
    {
      path: "/download",
      name: "Download",
      component: Download,
    },
    {
      path: "/sip",
      name: "SipCalculator",
      component: SipCalculator,
    },
    {
      path: "/career",
      name: "Listing",
      component: Listing,
    },
    {
      path: "/career-detail",
      name: "Details",
      component: Details,
    },
    {
      path: "/search",
      name: "Search",
      component: Search,
    },
    {
      path: "/splashscreen",
      name: "Splashscreen",
      component: Splashscreen,
    },
  ],
});
