<template>
  <Fragment>
    <section
      class="section-inner-hero position-relative overflow-hidden"
      id="animate-hero"
    >
      <div class="img-landscape-wide media-hero">
        <img :src="img" alt="" />
      </div>
      <div class="container d-none d-md-block">
        <div class="col-lg-5 col-md-7 p-0 textbox-hero">
          <div class="card p-4 position-absolute">
            <h3 class="text-primary mb-3">{{ title }}</h3>
            <p class="clamp__3">{{ des }}</p>
          </div>
        </div>
      </div>
    </section>
    <Breadcrumb :navs="nav" />
  </Fragment>
</template>

<script>
  import Breadcrumb from "../components/utility/breadcrumb";
  import { Fragment } from "vue-fragment";
  import anime from "animejs/lib/anime.es.js";

  export default {
    name: "InnerHero",
    components: { Breadcrumb, Fragment },
    props: {
      img: String,
      title: String,
      des: String,
      nav: [],
    },
    mounted() {
      const heroSection = document.querySelector("#animate-hero");
      if (heroSection) {
        const heroObserver = new IntersectionObserver((entries) => {
          if (entries[0].intersectionRatio > 0) {
            anime
              .timeline({
                easing: "cubicBezier(0.4, 0, 0.2, 1)",
              })
              .add({
                targets: "#animate-hero",
                opacity: [0, 1],
              })
              .add({
                easing: "cubicBezier(0.83, 0, 0.17, 1)",
                targets: ".media-hero",
                opacity: [0, 1],
                translateY: [200, 0],
              })
              .add(
                {
                  targets: ".textbox-hero",
                  opacity: [0, 1],
                  translateX: [-100, 0],
                },
                "-=200"
              )
              .add(
                {
                  targets: ".list-hero",
                  opacity: [0, 1],
                  translateX: [-100, 0],
                  delay: (el, i) => 100 * i,
                },
                "-=500"
              );
            heroObserver.unobserve(entries[0].target);
          }
        });
        heroObserver.observe(heroSection);
      }
    },
  };
</script>

