<template>
  <button
    @click="topFunction"
    class="btn-scroll"
    title="Go to top"
    v-if="scrollIndicator"
    style="display: block"
  >
    <i class="ic-caret-top"></i>
  </button>
</template>

<script>
  export default {
    name: "ScrollToTop",
    created() {
      window.addEventListener("scroll", this.scrollFunction);
    },
    destroyed() {
      window.removeEventListener("scroll", this.scrollFunction);
    },
    data() {
      return {
        scrollIndicator: false,
      };
    },
    methods: {
      scrollFunction() {
        if (
          document.body.scrollTop > 20 ||
          document.documentElement.scrollTop > 20
        ) {
          this.scrollIndicator = true;
        } else {
          this.scrollIndicator = false;
        }
      },

      topFunction() {
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });
      },
    },
  };
</script>
